body {
    font-family: Encode Sans !important;
}

:root {
    --main-primary: #22A39F;
    --primary-background: #f3efe0;
    --footer-background: #434242;
    --text-primary: #727878;
    --header-active: #001A14;
    --white: #FFFFFF;
}

h1 {
    font-style: normal;
    font-weight: bold;
    font-size: 28px;
    line-height: 33px;
    color: #22A39F;
    color: var(--main-primary);
}

h2 {
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
}

h3 {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 21px;
    color: #22A39F;
    color: var(--main-primary);
}

h4 {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #22A39F;
    color: var(--main-primary);
}

h5 {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #22A39F;
    color: var(--main-primary);
}

h6 {
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
    color: #22A39F;
    color: var(--main-primary);
}

.component-main-title {
    margin-bottom: 10px;
    font-style: normal;
    font-size: 54px;
    line-height: 60px;
    font-weight: 300;
    text-align: center;
    color: #22A39F;
    color: var(--main-primary);
}

.sub-text {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 23px;
    color: #727878;
    color: var(--text-primary);
}

.main-container {
    padding: 60px 10%;
    min-height: 100vh;
}

.ant-divider.main-divider {
    border: 1px solid;
    color: rgba(0, 0, 0, 0.3);
    margin: 24px 0 40px;
}

.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.margin-bottom-0 {
    margin-bottom: 0;
}


.scroll-button {
    position: absolute;
    bottom: 15px;
    left: 50%;
    height: 50px;
    width: 50px;
    z-index: 2;
    border-radius: 50%;
    cursor: pointer;
}

.scroll-button svg {
    width: 100%;
    height: 100%;
    fill: #ffffff;
    -webkit-animation: scrollIcon .5s linear 1s infinite alternate;
            animation: scrollIcon .5s linear 1s infinite alternate;
}

@-webkit-keyframes scrollIcon {
    from {
        transform: scale(1);

    }
    to {
        transform: scale(1.2);
    }
}

@keyframes scrollIcon {
    from {
        transform: scale(1);

    }
    to {
        transform: scale(1.2);
    }
}

.scroll-button:hover{
    transform: scale(1.2);
}

.scroll-to-top {
    bottom: 40px;
    right: -100px;
    position: fixed;
    background: #22A39F;
    background: var(--main-primary);
    border-radius: 50%;
    height: 45px;
    width: 45px;
    transition: .3s;
    cursor: pointer;
}

.scroll-to-top:hover {
    transform: scale(1.2);
}

.scroll-to-top.active {
    right: 40px;
}

.scroll-to-top:focus {
    outline: none;
}

.scroll-to-top svg {
    fill: #ffffff;
    width: 18px;
    height: 18px;
}

.display-flex {
    display: flex;
}

.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.justify-content-space-between {
    justify-content: space-between;
}

.align-items-center {
    align-items: center;
}

.flex-direction-column {
    flex-direction: column;
}

.flex-1 {
    flex: 1 1;
}

input, textarea {
    height: 42px;
    border-radius: 10px !important;
}

input, input:hover, input:focus,
textarea, textarea:hover, textarea:focus {
    border: none !important;
}




.background-container {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  background: #ffffff;
}

.background-container #tsparticles {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    rgb(53 67 74) 0%,
    rgb(151 156 126) 70%
  );
}

.main-profile-wrapper {
  z-index: 10;
  width: 60%;
}

.main-profile-wrapper {
  margin: auto;
  flex-direction: column;
}

.main-profile-wrapper,
.main-profile-wrapper__image-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-profile-wrapper__image-container {
  width: 200px;
  height: 200px;
  border: 5px solid #ffffff;
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: 10px;
}

.main-profile-wrapper__image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-profile-wrapper__body {
  text-align: center;
}

.main-profile-wrapper__body--name {
  font-style: normal;
  font-weight: 600;
  font-size: 40px;
  line-height: 60px;
  margin-bottom: 0.1em;
  color: #ffffff;
}

.main-profile-wrapper__body--title {
  font-style: italic;
  font-weight: 500;
  color: #ffffff;
}

.main-profile-wrapper__body--description {
  color: #ffffff;
  font-weight: 500;
}

.ant-divider.main-profile-wrapper__divider {
  border-top: 2px solid #ffffff;
}

.main-profile-wrapper__footer--social-media {
  display: flex;
}

.main-profile-wrapper__footer--social-media svg {
  fill: #ffffff;
}

@media only screen and (min-width: 768px) and (max-width: 1440px) {
}

@media only screen and (max-width: 576px) {
  .main-profile-wrapper {
    width: 80%;
  }
}

.header-main-container{
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    padding: 20px 20%;
    z-index: 10;
    background: var(--header-active);
}

.header-main-container p{
    color: white;
    margin: 0;
    cursor: pointer;
    transition: all .3s;
}

.header-main-container.active{
    background: var(--header-active);
}

.header-main-container p:hover{
    color: var(--main-primary);
    text-decoration: underline;
    margin-top: -5px;
}
.education {
    background: var(--primary-background);
}

.education-institutes {
    margin-bottom: 40px;
    display: flex;
    /* animation: 0.7s curtain cubic-bezier(.86,0,.07,1) 0.4s both; */
}

