.col-dates-wrapper{
    width: 50%;
}

.col-institute-wrapper{
    width: 50%;
}

.institute-main-container {
    display: flex;
    flex-direction: column;
    position: relative;
}

.institute-main-container::before {
    background: var(--main-primary);
    content: '';
    margin-left: -1px;
    position: absolute;
    top: 0;
    left: -40px;
    width: 2px;
    height: 100%;
}

.education-label{
    background: var(--main-primary);
    padding: 8px 15px;
    border: none;
    color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 0.5em;
}

.education-label span{
    font-size: 14px;
    font-weight: 800;
    letter-spacing: 2px;
}
.skills {
    background: var(--primary-background);
}

.skills-container {
    display: flex;
}


.grid-container {
    width: 100%;
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 20px;
    padding: 5px 5px 5px 0;
    /* animation: 0.7s curtain cubic-bezier(.86,0,.07,1) 0.4s both; */
}

.grid-item {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /*transition: 5s;*/
}

.grid-item:hover {
    background: #dddddd;
}

